.container{
    width: 100%;
    max-width: 40rem;
    margin: 10rem auto;
}


form{
    display: flex;
    flex-direction: column;
    color: white;
}

form legend{
    text-align: center;
}

form div{
    margin: 1% 0 5% 0;
}

form input{
    width: 100%;
    margin-top: 0.5%;
    height: 1.5rem;
    border-radius: 10px;
    border: 0;
    padding-left: 4px;
}

form .btn{
    
    background-color: rgb(97, 218, 251);
}

form .btn:hover{
    font-weight: 600;
}

h1{
    color: white;
    text-align: center;
}

